"use client"

import type React from "react"
import { useEffect, useState, useCallback } from "react"
import { useDropzone } from "react-dropzone"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { Loader2, Upload, X, Image as ImageIcon } from 'lucide-react'
import { DiagnosisResult } from "./diagnosis-result"
import type { DiagnosisResult as DiagnosisResultType } from "@/lib/types"

export function DiagnosisForm() {
  const [issue, setIssue] = useState("")
  const [image, setImage] = useState<File | null>(null)
  const [imagePreview, setImagePreview] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [result, setResult] = useState<DiagnosisResultType | null>(null)
  const [diagnosisId, setDiagnosisId] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const savedDiagnosis = localStorage.getItem("fixwise_diagnosis")
    if (savedDiagnosis) {
      try {
        const parsed = JSON.parse(savedDiagnosis)
        setResult(parsed.result)
        setDiagnosisId(parsed.diagnosisId)
        setIssue(parsed.issue || "")
      } catch (error) {
        console.error("Failed to restore diagnosis:", error)
        localStorage.removeItem("fixwise_diagnosis")
      }
    }
  }, [])

  const onDrop = useCallback((acceptedFiles: File[]) => {
    const file = acceptedFiles[0]
    if (file) {
      setImage(file)
      const reader = new FileReader()
      reader.onloadend = () => {
        setImagePreview(reader.result as string)
      }
      reader.readAsDataURL(file)
    }
  }, [])

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'image/*': ['.jpeg', '.jpg', '.png', '.gif', '.webp']
    },
    maxFiles: 1,
    maxSize: 5 * 1024 * 1024, // 5MB
  })

  const removeImage = () => {
    setImage(null)
    setImagePreview(null)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)
    setError(null)
    setResult(null)

    try {
      const formData = new FormData()
      formData.append("issue", issue)
      if (image) {
        formData.append("image", image)
      }

      const response = await fetch("/api/diagnose", {
        method: "POST",
        body: formData,
      })

      if (!response.ok) {
        throw new Error("Failed to get diagnosis")
      }

      const data = await response.json()
      setResult(data.diagnosis)
      setDiagnosisId(data.diagnosisId)
      
      localStorage.setItem("fixwise_diagnosis", JSON.stringify({
        result: data.diagnosis,
        diagnosisId: data.diagnosisId,
        issue: issue
      }))
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred")
    } finally {
      setIsLoading(false)
    }
  }

  if (result && diagnosisId) {
    return (
      <DiagnosisResult
        result={result}
        diagnosisId={diagnosisId}
        onReset={() => {
          setResult(null)
          setDiagnosisId(null)
          setIssue("")
          setImage(null)
          setImagePreview(null)
          localStorage.removeItem("fixwise_diagnosis")
        }}
      />
    )
  }

  return (
    <div className="space-y-4">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="relative">
          <Textarea
            id="issue"
            placeholder="Describe your device issue... (e.g., iPhone screen cracked, laptop won't turn on)"
            value={issue}
            onChange={(e) => setIssue(e.target.value)}
            required
            rows={3}
            className="resize-none rounded-full border-2 px-6 py-4 text-base shadow-lg focus-visible:ring-2"
          />
        </div>

        <div className="flex items-center justify-center gap-4">
          <Button type="submit" size="lg" disabled={isLoading || !issue.trim()}>
            {isLoading ? (
              <>
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                Analyzing...
              </>
            ) : (
              "Get AI Diagnosis"
            )}
          </Button>

          {!imagePreview ? (
            <>
              <Input id="image" type="file" accept="image/*" onChange={handleImageChange} className="hidden" />
              <Label htmlFor="image">
                <Button type="button" variant="outline" size="lg" asChild>
                  <span className="cursor-pointer">
                    <Upload className="mr-2 h-4 w-4" />
                    Add Photo
                  </span>
                </Button>
              </Label>
            </>
          ) : (
            <Button type="button" variant="outline" size="lg" onClick={removeImage}>
              <X className="mr-2 h-4 w-4" />
              Remove Photo
            </Button>
          )}
        </div>

        {imagePreview && (
          <div className="flex justify-center">
            <img
              src={imagePreview || "/placeholder.svg"}
              alt="Preview"
              className="h-32 w-32 rounded-lg object-cover shadow-md"
            />
          </div>
        )}

        {error && <p className="text-center text-sm text-destructive">{error}</p>}
      </form>
    </div>
  )
}
